import * as React from "react";
import { Component } from "react";
import Config from "./Config";
import Cookies from "js-cookie";

interface EnterDetailsProps {
  socket: any;
}

interface EnterDetailsState {
  name: string;
  phoneNumber: string;
  isEmpty: boolean;
  submitted: boolean;
}

class EnterDetails extends Component<EnterDetailsProps, EnterDetailsState> {
  constructor(props: EnterDetailsProps) {
    super(props);

    // Get data from cookies if available
    const name = Cookies.get("name") || "";
    const phoneNumber = Cookies.get("phoneNumber") || "";

    this.state = {
      name,
      phoneNumber,
      isEmpty: name === "" || phoneNumber === "",
      submitted: false,
    };
  }

  handleChange = (event: any) => {
    const { name, value } = event.target;

    this.setState(
      (prevState) => ({
        ...prevState,
        [name]: value,
        isEmpty: prevState.name === "" || prevState.phoneNumber === "",
      }),
      () => {
        // Save changes in cookies
        Cookies.set("name", this.state.name, { expires: 7 });
        Cookies.set("phoneNumber", this.state.phoneNumber, { expires: 7 });
      }
    );
  };

  handleKeyPress = (event: any) => {
    if (event.key === "Enter" && !this.state.isEmpty) {
      this.handleSubmit(event);
    }
  };

  handleSubmit = (event: any) => {
    event.preventDefault();
    // Save data to the database
    this.saveToDatabase(this.state.name, this.state.phoneNumber);
    this.setState({
      submitted: true,
    });
  };

  saveToDatabase(name: string, phoneNumber: string) {
    // Simulate saving to a database
    console.log("Saving to database:", { name, phoneNumber });
    // Replace this with an actual API call to store data in your database
  }

  render() {
    if (!this.state.submitted) {
      let myStyles: React.CSSProperties = {
        visibility: this.state.isEmpty ? "hidden" : "visible",
      };

      return (
        <div className="enter-details-container">
          <div className="enter-details">
            <p className="enter-details-question">Hi. Please enter your details:</p>
            <input
              id="enter-name"
              type="text"
              name="name"
              value={this.state.name}
              placeholder="Your Name"
              onChange={this.handleChange}
              onKeyPress={this.handleKeyPress}
              spellCheck="false"
              autoFocus
            />
            <br />
            <input
              id="enter-phone"
              type="tel"
              name="phoneNumber"
              value={this.state.phoneNumber}
              placeholder="Your Phone Number"
              onChange={this.handleChange}
              onKeyPress={this.handleKeyPress}
            />
            <br />
            <div style={myStyles}>
              <button onClick={this.handleSubmit}>OK</button>
            </div>
          </div>
        </div>
      );
    } else {
      return (
		<div className="config-container">
		  <div className="config-content">
			<h1 className="config-header">Welcome!</h1>
			<p className="config-details">Name: {this.state.name}</p>
			<p className="config-details">Phone Number: {this.state.phoneNumber}</p>
			<button className="config-button" onClick={this.handleSubmit}>
			  Proceed
			</button>
		  </div>
		</div>
	  );
    }
  }
}

export default EnterDetails;
